/**
 * Parse the time to string
 * @param {(Object|string|number)} time
 * @param {string} cFormat
 * @returns {string}
 */
export function parseTime(time, cFormat) {
  if (arguments.length === 0) {
    return null
  }
  const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}'
  let date
  if (typeof time === 'object') {
    date = time
  } else {
    if ((typeof time === 'string') && (/^[0-9]+$/.test(time))) {
      time = parseInt(time)
    }
    if ((typeof time === 'number') && (time.toString().length === 10)) {
      time = time * 1000
    }
    date = new Date(time)
  }
  const formatObj = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  }
  const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
    let value = formatObj[key]
    // Note: getDay() returns 0 on Sunday
    if (key === 'a') { return ['日', '一', '二', '三', '四', '五', '六'][value] }
    if (result.length > 0 && value < 10) {
      value = '0' + value
    }
    return value || 0
  })
  return time_str
}

/**
  *   两个日期的时间差
  *   @param {string} start
  *   @param {string} end
  */
export function countDate(start, end) {
  const date1 = new Date(start)
  const date2 = new Date(end)
  const date = (date2.getTime() - date1.getTime()) / (1000 * 60 * 60 * 24) /* 不用考虑闰年否*/
  return date
}

/**
  *   两个时间的某项单位差 (默认是小时)
  *   @param {string} start
  *   @param {string} end
  *   @param {string} unit  单位 ： 'H' 'M' 'S'
  */
export function countTimer(start, end, unit) {
  const date1 = new Date(`2018-10-10 ${start}:00`)
  const date2 = new Date(`2018-10-10 ${end}:00`)
  var date3 = date2.getTime() - new Date(date1).getTime() // 时间差的毫秒数

  if (unit === 'S') {
    return Number(date3) / 1000
  }

  var subMinutes = Math.floor(date3 / (60 * 1000)) // 获取总共的分钟差
  if (unit === 'M') {
    return Number(subMinutes)
  }

  // 计算出相差天数
  // var days = Math.floor(date3 / (24 * 3600 * 1000))

  // 计算出小时数
  var leave1 = date3 % (24 * 3600 * 1000) // 计算天数后剩余的毫秒数
  var hours = Math.floor(leave1 / (3600 * 1000))
  // 计算相差分钟数
  var leave2 = leave1 % (3600 * 1000) // 计算小时数后剩余的毫秒数
  var minutes = (Math.floor(leave2 / (60 * 1000)) / 60).toFixed(2)
  // 计算相差秒数
  // var leave3 = leave2 % (60 * 1000) // 计算分钟数后剩余的毫秒数
  // var seconds = Math.round(leave3 / 1000)
  return Number(hours) + Number(minutes)
}

/**
 * 节流函数
 * @param {Function} func
 * @param {number} wait
 * @param {boolean} immediate
 * @return {*}
 */
export function debounce(func, wait, immediate) {
  let timeout, args, context, timestamp, result

  const later = function() {
    // 据上一次触发时间间隔
    const last = +new Date() - timestamp

    // 上次被包装函数被调用时间间隔 last 小于设定时间间隔 wait
    if (last < wait && last > 0) {
      timeout = setTimeout(later, wait - last)
    } else {
      timeout = null
      // 如果设定为immediate===true，因为开始边界已经调用过了此处无需调用
      if (!immediate) {
        result = func.apply(context, args)
        if (!timeout) context = args = null
      }
    }
  }

  return function(...args) {
    context = this
    timestamp = +new Date()
    const callNow = immediate && !timeout
    // 如果延时不存在，重新设定延时
    if (!timeout) timeout = setTimeout(later, wait)
    if (callNow) {
      result = func.apply(context, args)
      context = args = null
    }
    return result
  }
}

Math.easeInOutQuad = function(t, b, c, d) {
  t /= d / 2
  if (t < 1) {
    return c / 2 * t * t + b
  }
  t--
  return -c / 2 * (t * (t - 2) - 1) + b
}

/**
 * @name: 滚动函数
 * @param {number} amount
 */
export function move(amount) {
  document.documentElement.scrollTop = amount
  document.body.parentNode.scrollTop = amount
  document.body.scrollTop = amount
}

var requestAnimFrame = (function() {
  return window.requestAnimationFrame || window.webkitRequestAnimationFrame || window.mozRequestAnimationFrame || function(callback) { window.setTimeout(callback, 1000 / 60) }
})()

/**
 * @name: 计算位置
 * @return: number
 */
export function position() {
  return document.documentElement.scrollTop || document.body.parentNode.scrollTop || document.body.scrollTop
}

/**
 * @name: 滚动倒顶部
 * @param {number} to
 * @param {number} duration
 * @param {function} callback
 */
export function scrollTop(to, duration = 500, callback) {
  const start = position() // 滚动距离
  const change = to - start
  const increment = 20 // 增量
  let currentTime = 0
  // 滚动动画函数
  const animateScroll = function() {
    currentTime += increment
    // 动画执行到当前帧所经过的时间,起始值,需要变化的量,动画的总时间
    var val = Math.easeInOutQuad(currentTime, start, change, duration)
    // 滚动页面(页面滚动量)
    move(val)
    if (currentTime < duration) {
      // 请求动画帧
      requestAnimFrame(animateScroll)
    } else {
      if (callback && typeof (callback) === 'function') {
        callback()
      }
    }
  }
  animateScroll()
}

export function noValue(value) {
  return [null, 'null', undefined, 'undefined', ''].includes(value)
}
export function checkAllKeyHasValue(objectName) {
  let allKeyHasValue = true
  objectName.forEach(item => {
    const values = Object.values(item)
    if (
      values.some(item02 => {
        return noValue(item02)
      })
    ) {
      allKeyHasValue = false
    }
  })
  return allKeyHasValue
}
export function chinaDate(times, fengefu) {
  fengefu = fengefu || '-'
  const time = new Date(times)
  const nian = time.getFullYear()
  const yue = ('00' + (time.getMonth() + 1)).slice(-2)
  const ri = ('00' + time.getDate()).slice(-2)
  return nian + fengefu + yue + fengefu + ri
}
/**
 * 简单版本get参数 并未支持数组 仅限 “ 最简单 ” 版对象
 *  如{name:'张三',age:18} ==>"?name=张三&age=18"
 */
export function simpleQs(res = {}) {
  if (Object.prototype.toString.call(res) !== '[object Object]') {
    console.error('simpleQs的参数必须是对象')
    return '?error=参数错误simpleQs的参数必须是对象'
  }
  let str = '?'
  for (var key in res) {
    res[key] = res[key] === null ? '' : res[key]
    str += '' + key + '=' + res[key] + '&'
  }
  return str.slice(0, -1)
}
// 防抖函数
export function debounceS(fn, delay = 1000, immediate = false) {
  let timer = null
  let isImmediate = false
  return function(...args) {
    if (immediate && !isImmediate) {
      fn.apply(this, args)
      isImmediate = true
    } else {
      if (timer) clearTimeout(timer)
      timer = setTimeout(() => {
        fn.apply(this, args)
        timer = null
        isImmediate = false
      }, delay)
    }
  }
}

// 防抖函数
export function _debounce(fn, delay = 1000, immediate = false) {
  let timer = null
  let isImmediate = false
  return function(...args) {
    if (immediate && !isImmediate) {
      fn.apply(this, args)
      isImmediate = true
    } else {
      if (timer) clearTimeout(timer)
      timer = setTimeout(() => {
        fn.apply(this, args)
        timer = null
        isImmediate = false
      }, delay)
    }
  }
}
// 简单防抖
export function s_debounce(fn, delay) {
  let timer = null
  return function() {
    clearTimeout(timer)
    timer = setTimeout(() => {
      fn.apply(this, arguments)
    }, delay)
  }
}
