<template>
  <el-dialog
    :title="title"
    top="15vh"
    :visible="value"
    :show-close="true"
    width="30%"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="Form" :model="form" :rules="rules" label-width="120px" label-position="right">
      <el-form-item label="名称：" prop="columnsTemplateName">
        <el-input v-model="form.columnsTemplateName" placeholder="请输入模板名称" clearable class="filter-item" style="width: 178px;" />
      </el-form-item>
    </el-form>
    <div slot="footer">
      <el-button type="primary" @click="handleSave('Form')">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
const INIT_FORM = () => ({
  columnsTemplateName: ''
})
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    title: {
      type: [String, Number],
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      visible: false,
      form: INIT_FORM(),
      rules: {
        columnsTemplateName: [
          { required: true, message: '请输入模板名称', trigger: 'blur' }
        ]
      }
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = INIT_FORM()
        } else {
          this.form = { columnsTemplateId: value.id, columnsTemplateName: value.templateName }
        }
      },
      immediate: true
    }
  },
  methods: {
    // 表单提交
    handleSave(formName) {
      Promise.all([
        this.$refs[formName].validate()
          .then(res => {
            this.$emit('confirm', { ...this.form })
            this.beforeClose()
          })
          .catch(err => {
            console.log('err', err)
            return Promise.reject(err)
          })
      ])
    },
    // 弹窗关闭前操作
    beforeClose(done) {
      this.$refs['Form'].resetFields()
      this.form = INIT_FORM()
      typeof done === 'function' && done()
      this.$emit('input', false)
    },
    // 弹窗打开初始化
    initSelect() {
    }
  }
}
</script>

<style lang="scss" scoped>
</style>

