<template>
  <el-row :class="`pagination-container ${className}`">
    <el-pagination
      :class="pagiationClassName"
      :background="background"
      :current-page.sync="currentPage"
      :page-sizes="pageSizes"
      :page-size="pageSize"
      :layout="layout"
      :total="total"
      :hide-on-single-page="hideOnSinglePage"
      :small="small"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    />
  </el-row>
</template>

<script>
import { scrollTop } from '@/utils'

export default {
  name: 'PaginationCom',
  components: {},
  props: {
    className: {
      type: String,
      default: 'clearfix'
    },
    total: {
      required: true,
      type: Number,
      default: 10
    },
    page: {
      type: Number,
      default: 1
    },
    limit: {
      type: Number,
      default: 50
    },
    pageSizes: {
      type: Array,
      default: () => {
        // return [10, 20, 30, 50, 100]
        return [50, 100, 150]
      }
    },
    layout: {
      type: String,
      default: 'total, prev, pager, next, jumper, sizes'
    },
    background: {
      type: Boolean,
      default: false
    },
    hideOnSinglePage: {
      type: Boolean,
      default: false
    },
    autoScroll: {
      type: Boolean,
      default: true
    },
    small: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    pagiationClassName() {
      if (this.className === 'clearfix') {
        return 'fr'
      } else {
        return ''
      }
    },
    currentPage: {
      get() {
        return this.page
      },
      set(val) {
        this.$emit('update:page', val)
      }
    },
    pageSize: {
      get() {
        return this.limit
      },
      set(val) {
        this.$emit('update:limit', val)
      }
    }
  },
  methods: {
    handleSizeChange(val) {
      this.$emit('update:limit', val)
      this.$emit('pagination')
      if (this.autoScroll) {
        scrollTop(0)
      }
    },
    handleCurrentChange(val) {
      this.$emit('pagination')
      if (this.autoScroll) {
        scrollTop(0)
      }
    }
  }
}
</script>
