<template>
  <div>
    <el-select
      v-model="scope[column.prop]"
      :disabled="column.setDisabled ? column.setDisabled(scope) : false"
      filterable
      clearable
      :placeholder="column.placeholder || '请选择内容'"
      @change="column.selectChange ? column.selectChange(scope) : null"
    >
      <el-option
        v-for="item in column.getOptions(scope)"
        :key="item[column.optionValue]"
        :label="item[column.optionLabel]"
        :value="item[column.optionValue]"
      />
    </el-select>
  </div>
</template>

