<template>
  <el-table
    :id="refName"
    :ref="refName"
    :key="refreshKey"
    v-loading="loading"
    :data="tableData"
    border
    stripe
    highlight-current-row
    tooltip-effect="dark"
    style="width: 100%"
    :row-key="rowKey"
    :header-cell-style="{background:'#eef1f6',color:'#606266'}"
    :row-class-name="rowClassName"
    :row-style="rowStyle"
    :cell-style="cellStyle"
    :header-row-style="{height: '36px'}"
    :tree-props="treeProps"
    :expand-row-keys="expandRowKeys"
    :height="tableHeight"
    cell-class-name="table_cell"
    :span-method="rowSpanMethod"
    @select="handleSelect"
    @select-all="handleSelectAll"
    @selection-change="handleSelectionChange"
    @row-click="handleRowClick"
    @sort-change="sortChange"
    @expand-change="expandChange"
  >

    <!-- 扩展信息 expand expandList -->
    <el-table-column v-if="expandList.length" type="expand">
      <template slot-scope="props">
        <el-form v-if="expandType === 'form'" label-position="left" class="table-expand">
          <el-form-item
            v-for="(expandItem) in expandList"
            :key="expandItem.prop"
            :label="`${expandItem.label}：`"
          >
            <!-- 展示字段： props.row[expandItem.prop] -->
            <component :is="getComponentName(expandItem)" :column="expandItem" :scope="props.row" />
          </el-form-item>
        </el-form>
        <div v-if="expandType === 'table'">
          <slot name="expand-table" :scope="props" />
        </div>
      </template>
    </el-table-column>

    <!-- 多选框 selection reserve-selection 在翻页时记录勾选内容 -->
    <el-table-column
      v-if="selection"
      type="selection"
      :reserve-selection="true"
      align="center"
      width="55"
      :fixed="selectionFixed"
      :selectable="selectable ? selectable : null"
    />

    <!-- 序号 index -->
    <el-table-column
      v-if="index"
      type="index"
      min-width="60"
      align="center"
      label="序号"
      :fixed="indexFixed"
    />

    <!-- 文字列表和特殊组件列表: 兼容多级表头，使用递归组件 -->
    <!-- 文字列表 -->
    <template v-for="column in columnList">
      <!-- 特殊组件 -->
      <table-column
        :key="column.prop"
        :column="column"
      />
    </template>

    <!-- 操作 -->
    <el-table-column v-if="showOperate" :fixed="operateFixed" label="操作" :width="operationWidth" :align="operateColumnAlign">
      <template slot="header">
        <div class="table-operation-header">
          <div>{{ operateColumnName }}</div>
          <!-- 操作表头的功能按钮 -->
          <div class="table-operation-icon-container">
            <!-- 刷新列表 -->
            <refresh-icon @click.native="handleRefresh" />
            <!-- 列排序 -->
            <system-icon v-show="tableColumnSort" @click.native="sortDialogVisible = true" />
          </div>
        </div>
      </template>
      <!-- 操作按钮插槽 -->
      <template slot-scope="scope">
        <div style="width: 100%;'text-align': center;">
          <slot :row="scope.row" :index="scope.$index" />
        </div>
      </template>
    </el-table-column>

    <!-- 空数据展示文本 -->
    <template v-if="showEmptyTextSlot" #empty>
      <slot name="empty-slot">暂无数据</slot>
    </template>

    <!-- 表头排序弹窗组件 -->
    <sort-dialog
      v-model="sortDialogVisible"
      :sort-data="tableColumnSortData"
      v-bind="$attrs"
      v-on="$listeners"
      @submit="sortDialogSubmit"
    />

  </el-table>
</template>

<script>
import TableColumnComponents from './components' // 批量引入的Table行内组件
import TableColumn from './TableColumn.vue'
import RefreshIcon from './RefreshIcon.vue'
import SystemIcon from './SystemIcon'
import SortDialog from './SortDialog.vue'

export default {
  name: 'CustomTableColumn',
  components: {
    TableColumn,
    RefreshIcon,
    SystemIcon,
    SortDialog,
    ...TableColumnComponents
  },
  props: {
    refreshKey: {
      type: Number,
      default: 1
    },
    refName: { // ref名 和 id
      type: String,
      default: 'multipleTable'
    },
    /*
    *   table的高度配置，可为number和 String；
    *   如果为number直接设置table的高度，
    *   字符串： 'fullscreen' 全屏高度  'half' 半屏幕  'fullscreenTab' 全屏+有tab的高度  'halfTab' 半屏+有tab的高度
    *   字符串配置： 'extraFullMinus'  全屏额外减去   'extraHalfMinus' 半屏额外减去  需要配合 @props tableHeightMinus 实现全屏或者半屏幕减去固定高度来固定table的高
    */
    tableHeightType: {
      type: [Number, String],
      default: 'fullscreen'
    },
    tableHeightMinus: {
      type: Number,
      default: 0
    },
    hasPagination: { // 是否关联分页，有分页需要动态调节 table 的高度
      type: Boolean,
      default: true
    },
    hasTopQuery: { // 是否表格上部分有额外的按钮和查询，有的话需要动态调节 table 的高度
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    },
    maxHeight: {
      type: String,
      default: '100%'
    },
    index: {
      type: Boolean,
      default: true
    },
    indexFixed: {
      type: [Boolean, String],
      default: false
    },
    rowKey: {
      type: String,
      default: 'id'
    },
    rowClassName: {
      type: [Function, String],
      default: () => ''
    },
    rowStyle: {
      type: [Function, String],
      default: () => ''
    },
    cellStyle: {
      type: [Function, String],
      default: () => ''
    },
    selection: {
      type: Boolean,
      default: false
    },
    multiple: {
      type: Boolean,
      default: true
    },
    selectionFixed: {
      type: Boolean,
      default: true
    },
    tableSelection: {
      type: Array,
      default: () => []
    },
    expand: {
      type: Boolean,
      default: false
    },
    expandList: {
      type: Array,
      default: () => {
        return []
      }
    },
    expandType: {
      type: String,
      default: 'form'
    },
    treeProps: {
      type: Object,
      default: () => {
        return { children: 'children', hasChildren: 'hasChildren' }
      }
    },
    expandRowKeys: {
      type: Array,
      default: () => []
    },
    tableData: {
      type: Array,
      default: () => {
        return []
      }
    },
    columnList: {
      type: Array,
      default: () => {
        return []
      }
    },
    tableColumnSort: {
      type: Boolean,
      default: true
    },
    tableColumnSortData: {
      type: Array,
      default: () => {
        return []
      }
    },
    operateFixed: {
      type: [Boolean, String],
      default: 'right'
    },
    showOperate: {
      type: Boolean,
      default: true
    },
    operationWidth: {
      type: Number,
      default: 270
    },
    operateColumnName: {
      type: String,
      default: '操作'
    },
    operateColumnAlign: {
      type: String,
      default: 'center'
    },
    rowMergeArr: {
      type: Array,
      default: () => []
    },
    // 仅对 type=selection 的列有效，类型为 Function，Function 的返回值用来决定这一行的 CheckBox 是否可以勾选
    selectable: {
      type: Function,
      default: () => true
    },
    // 是否执行初始化选中
    selectedInit: {
      type: Boolean,
      default: true
    },
    showEmptyTextSlot: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      tableHeight: '400',
      renderKey: 1,
      sortable: null,
      sortDialogVisible: false,
      tableSelectionList: [],
      mergeObj: {} // 存放行合并的数据
    }
  },
  watch: {
    $route(route) {
      // if you go to the redirect page, do not update table layout
      this.doLayout()
    },
    tableSelection: {
      handler: function(newVal) {
        this.tableSelectionList = [...newVal]
      },
      immediate: true,
      deep: true
    },
    tableData: {
      handler: function(newVal) {
        this.renderKey++
        this.rowMergeArr.length ? this.rowspan() : null
        if (this.selectedInit) {
          this.$nextTick(() => {
            this.initSelection()
            this.doLayout()
          })
        }
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    this.handleInitTableHeight()
  },
  updated() {
    console.log('handle table component updated')
    this.doLayout()
  },
  methods: {
    /*
    *  设置默认表格高度
    */
    handleInitTableHeight() {
      let height = 0
      if (typeof this.tableHeightType === 'number') { // 用户设置固定的数字高度，直接赋值
        height = this.tableHeightType
      } else {
        const innerHeight = window.innerHeight //  视口高度
        const table = document.getElementById(this.refName)
        const offsetTop = table.getBoundingClientRect().top // table相对视窗上部的距离
        const tableHeaderHeight = table.querySelector('.el-table__header-wrapper').offsetHeight // 表头的像素高度
        if (typeof this.tableHeightType === 'string' && this.tableHeightType === 'half') { // 半屏幕高度
          const h = (innerHeight - offsetTop) / 2 - tableHeaderHeight
          height = this.hasPagination ? Math.floor(h - 74) : Math.floor(h - 35)
        } else if (typeof this.tableHeightType === 'string' && this.tableHeightType === 'halfTab') { // 半屏+tap
          const h = (innerHeight - offsetTop) / 2 - tableHeaderHeight - 40
          height = this.hasPagination ? Math.floor(h - 74) : Math.floor(h - 35)
        } else if (typeof this.tableHeightType === 'string' && this.tableHeightType === 'fullscreenTab') { // 全屏+tap
          const h = innerHeight - offsetTop - tableHeaderHeight - 40
          height = this.hasPagination ? Math.floor(h - 56) : Math.floor(h - 10)
          if (this.hasTopQuery) { // 表格上部分有额外的按钮和查询行
            height = height - 40
          }
        } else if (typeof this.tableHeightType === 'string' && this.tableHeightType === 'fullscreen') { // 全屏高度
          const h = innerHeight - offsetTop - tableHeaderHeight
          height = this.hasPagination ? Math.floor(h - 56) : Math.floor(h - 10)
          if (this.hasTopQuery) { // 表格上部分有额外的按钮和查询行
            height = height - 40
          }
        } else if (typeof this.tableHeightType === 'string' && this.tableHeightType === 'extraHalfMinus') { // 半屏幕-固定高
          const h = (innerHeight - offsetTop) / 2 - tableHeaderHeight - this.tableHeightMinus
          height = this.hasPagination ? Math.floor(h - 74) : Math.floor(h - 35)
        } else { // 全屏幕 - 固定高
          const h = innerHeight - offsetTop - tableHeaderHeight - this.tableHeightMinus
          height = this.hasPagination ? Math.floor(h - 56) : Math.floor(h - 10)
          if (this.hasTopQuery) { // 表格上部分有额外的按钮和查询行
            height = height - 40
          }
        }
      }
      this.$nextTick(() => {
        // this.tableHeight = window.innerHeight - 240
        this.tableHeight = height
      })
    },
    sortDialogSubmit() {
      console.log('表格排序更新')
    },
    // 刷新列表
    handleRefresh() {
      this.$emit('refresh')
    },
    // 列点击
    handleRowClick(row, column, cell, event) {
      this.$emit('rowClick', { row, column, cell, event })
    },
    sortChange({ column, prop, order }) {
      this.$emit('sortChange', { column, prop, order })
    },
    expandChange(row) {
      this.$emit('expandChange', row)
    },
    // 拼接列组件的名称
    getComponentName(column) {
      const is = column.module
      return `${is.charAt(0).toUpperCase()}${is.slice(1)}Com`
    },
    // 初始化搜索方法
    initSelection() {
      const ids = this.tableSelection.map(i => i[this.rowKey])
      const rowKey = this.rowKey
      this.tableData.forEach(row => {
        function check(row) {
          const id = row[rowKey]
          return ids.some(i => i === id)
        }
        if (check(row)) {
          this.$nextTick(() => {
            this.$refs[this.refName].toggleRowSelection(row, true)
          })
        } else {
          this.$nextTick(() => {
            this.$refs[this.refName].toggleRowSelection(row, false)
          })
        }
      })
    },
    // 多选表格，清空用户的选择
    clearSelection() {
      this.$refs[this.refName].clearSelection()
    },
    // 获取多选的数组
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs[this.refName].toggleRowSelection(row)
        })
      } else {
        this.$refs[this.refName].clearSelection()
      }
    },
    /*
    *  多选全选事件
    *  selection 目前所有勾选的数据
    * */
    handleSelectAll(selection) {
      // 当💰状态为单选的时候，点击全选清空勾选列表
      if (!this.multiple) {
        this.$refs[this.refName].clearSelection()
        this.tableSelectionList = new Array(0)
      }
    },
    /*
    *  多选勾选事件，发送的内容为
    *  tableSelection 目前所有勾选的数据
    * */
    handleSelectionChange(val) {
      if (this.multiple) {
        this.tableSelectionList = val
        console.log('多选选中，table组件上传数据', this.tableSelectionList)
        this.$emit('selectionChange', this.tableSelectionList)
      }
    },
    /*
    * 多选框的勾选事件
    * 1. 如果multiple为false，则为单选，单选需要清空所有选项，并勾选当前
    */
    handleSelect(selection, row) {
      // 勾选获取当前数据(私有化场景，获取勾选动作)
      this.$emit('dynamicRow', row)
      if (!this.multiple) {
        console.log('单选选中，table组件上传数据', [row])
        // 先清除所有选中的
        this.$refs[this.refName].clearSelection()
        // 当前的点击实现勾选
        this.$refs[this.refName].toggleRowSelection(row)
        this.$emit('selectionChange', [row])
      }
    },
    /*
    * 设置默认选择 高亮行的选中状态
    * @params row Object 数据
    * @params type Boolean 是否选中
    */
    handleSetCurrentRow(row, type) {
      this.$refs[this.refName].setCurrentRow(row, type)
    },
    /*
    * 设置每一行的类名，根据props rowClassName 传递进的函数进行更改，rowClassName传递为String会全部设置，传递为Function会根据返回值设置
    */
    handleRowClassName({ row, rowIndex }) {
      return typeof this.rowClassName === 'string' ? this.rowClassName : this.rowClassName({ row, rowIndex })
    },
    /*
    *  Tip行合并
    */
    rowspan() {
      for (const key of this.rowMergeArr) {
        let count = 0
        this.mergeObj[key] = []
        this.tableData.forEach((item, index) => {
          if (index === 0) {
            this.mergeObj[key].push(1)
          } else {
            if (item[key] === this.tableData[index - 1][key]) {
              this.mergeObj[key][count] += 1
              this.mergeObj[key].push(0)
            } else if (item[this.rowKey] === this.tableData[index - 1][this.rowKey]) {
              this.mergeObj[key][count] += 1
              this.mergeObj[key].push(0)
            } else {
              count = index
              this.mergeObj[key].push(1)
            }
          }
        })
      }
    },
    // Tip 表格合并方法
    rowSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (this.rowMergeArr.indexOf(column.property) !== -1) {
        if (this.mergeObj[column.property][rowIndex]) {
          return {
            rowspan: this.mergeObj[column.property][rowIndex],
            colspan: 1
          }
        } else {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      } else {
        return {
          rowspan: 1,
          colspan: 1
        }
      }
    },
    /*
    *  对 Table 进行重新布局，当 Table 或其祖先元素由隐藏切换为显示时 调用此方法重新布局组件，解决fixed高度问题
    */
    doLayout() {
      console.log('table 组件重置～')
      this.$nextTick(() => {
        if (this.$refs[this.refName]) {
          this.$refs[this.refName].doLayout()
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.table-expand {
    font-size: 0;
    margin-left: 63px;
}
.table-expand label {
    width: 190px;
    color: #99a9bf;
}
.table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 100%;
}
::v-deep .el-table::before {
  z-index: inherit; // 规定应该从父元素继承 z-index 属性的值。
}
</style>
