<template>
  <el-dialog
    :visible="value"
    :show-close="false"
    :close-on-click-modal="false"
    :append-to-body="true"
    width="750px"
    :lock-scroll="false"
    :before-close="beforeClose"
    @opened="opened"
    @open="initSelect"
  >
    <template slot="title">
      <div class="dialog-header-wrapper">
        <h5 class="dialog-header-title">自定义显示列项</h5>
        <!-- <div class="dialog-header-extra-title">列项显示不得少于5项，最多支持自定义10个列项，灰色选中列不支持隐藏和排序</div> -->
        <div class="dialog-header-extra-title">列项显示不得少于5项，灰色选中列不支持隐藏和排序</div>
      </div>
    </template>

    <el-scrollbar class="scroll-dialog">
      <div class="clearfix top-filter">
        <el-form ref="filterItem" :model="query" inline>
          <el-form-item prop="columnsTemplateId">
            <el-select
              v-model="query.columnsTemplateId"
              class="filter-item"
              placeholder="表格排序模板"
              filterable
              clearable
              style="width: 178px;"
              @change="search"
            >
              <el-option v-for="(item, index) in sortTableTemplate" :key="index" :label="item.templateName" :value="item.id" />
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <el-table
        ref="sortTable"
        v-loading="loading"
        :data="tableData"
        style="width: 100%"
        row-key="prop"
        border
        :row-class-name="rowClassName"
        :header-cell-style="{background:'#eef1f6',color:'#606266',lineHeight: '10px'}"
      >
        <el-table-column
          prop="show"
          label="显示"
          width="56"
          align="center"
        >
          <template slot-scope="scope">
            <el-checkbox v-model="scope.row.show" :disabled="scope.row.forceShow" />
          </template>
        </el-table-column>
        <el-table-column
          prop="label"
          label="列名"
          min-width="100"
          align="center"
        />
        <el-table-column prop="sortable" label="排序" align="center">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.sortable" active-color="#409EFF" inactive-color="#DCDFE6" />
          </template>
        </el-table-column>
        <el-table-column v-if="!['STOCK_OUT_FORM', 'STOCK_IN_FORM'].includes($attrs['form-ident'])" prop="extend " label="扩展" align="center">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.extend" :disabled="scope.row.forceExtend" active-color="#409EFF" inactive-color="#DCDFE6" />
          </template>
        </el-table-column>
        <el-table-column label="拖动调整顺序" align="center" class="table-operation-icon">
          <template>
            <svg-icon icon-class="threeheng" />
          </template>
        </el-table-column>
        <el-table-column label="列宽" prop="width" align="center" :width="130">
          <template slot-scope="scope">
            <el-input-number v-model="scope.row.width" :min="0" label="请填写" style="width: 100%;" />
          </template>
        </el-table-column>
      </el-table>
    </el-scrollbar>
    <div slot="footer">
      <el-button type="text" @click="handleDefault">恢复默认</el-button>
      <el-button type="primary" @click="saveTem">保存模板</el-button>
      <el-button type="primary" @click="templateManagement">模板管理</el-button>
      <el-button type="primary" @click="confirmSortDialog">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
    <sort-tem-add v-model="sortTemVisible" title="表格排序模板新增" :edit-type="editType" @confirm="sortTemSave" />
    <sort-tem-list v-model="sortTemVisible1" v-bind="$attrs" @confirm="getSortTableTem" />
  </el-dialog>
</template>

<script>
import _ from 'lodash'
import Sortable from 'sortablejs'
import SortTemAdd from './SortTemAdd'
import SortTemList from './SortTemList'
import { getTableColumnSortDataApi as getListApi, updateTableColumnSortDataApi as updateListApi, saveSortTableTemplateApi, getSortTableTemplateApi, getSortTableTemplateDetailApi } from '@/apis/modules/common'

export default {
  name: 'SortDialog',
  components: {
    SortTemAdd,
    SortTemList
  },
  props: {
    value: {
      type: Boolean,
      default: false
    },
    sortData: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
      title: '自定义显示列',
      loading: false,
      defaultData: [],
      tableData: [],
      sortTemVisible: false,
      sortTemVisible1: false,
      editType: 'add',
      sortTableTemplate: [],
      query: {
        columnsTemplateId: ''
      }
    }
  },
  watch: {
    sortTableTemplate: {
      handler(val) {
        const bool = val.some(c => c.id === this.query.columnsTemplateId)
        !bool && (this.query.columnsTemplateId = '', this.search())
      },
      deep: true
    }
  },
  methods: {
    // 搜索
    search() {
      this.loading = true
      this.init1().then(res => {
        this.tableData = _.cloneDeep(res)
        this.defaultData = _.cloneDeep(res)
        this.$nextTick(() => {
          this.initSortTable()
        })
        this.loading = false
      }).catch(err => {
        console.log(err)
        this.loading = false
      })
    },
    init1() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getSortTableTemplateDetailApi({
          ...this.query,
          formIdent: this.$attrs['form-ident']
        }).then(res => {
          const { code, data } = res
          if (!code) {
            // 去除扩展字段
            const list = data.filter(f => !f.param)
            resolve(list)
          }
          this.loading = false
          reject(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          formIdent: this.$attrs['form-ident']
        }).then(res => {
          const { code, data } = res
          if (!code) {
            // 去除扩展字段
            const list = data.filter(f => !f.param)
            resolve(list)
          }
          this.loading = false
          reject(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 弹窗初始化
    initSelect() {
      this.query.columnsTemplateId = ''
      this.getSortTableTem()
    },
    // 获取表格排序模板下拉数据
    getSortTableTem() {
      getSortTableTemplateApi({
        formIdent: this.$attrs['form-ident']
      }).then(res => {
        const { code, data } = res
        if (!code) {
          this.sortTableTemplate = data
        }
      }).catch(err => {
        console.log(err)
      })
    },
    // 表格排序模板保存
    sortTemSave(params = {}) {
      saveSortTableTemplateApi({
        ...params,
        formIdent: this.$attrs['form-ident'],
        formHeadSets: this.tableData.map((item, key) => {
          return { ...item, sort: key + 1 }
        })
      }).then(res => {
        const { code } = res
        if (!code) {
          this.$notify.success({
            title: '成功',
            message: `模板保存成功`
          })
          this.getSortTableTem()
          // this.$listeners.refresh()
          // this.beforeClose()
        }
      }).catch(err => {
        console.log(err)
      })
    },
    // 保存模板
    saveTem() {
      if (!this.query.columnsTemplateId) {
        this.sortTemVisible = true
        return
      }
      this.sortTemSave({ columnsTemplateId: this.query.columnsTemplateId })

      // columnsTemplateName
    },
    // 模板管理
    templateManagement() {
      this.sortTemVisible1 = true
    },
    // 确定
    confirmSortDialog() {
      if (this.tableData.filter(item => item.show).length < 5) {
        this.$notify.warning({
          title: '警告',
          message: `列项显示不得少于5项！`
        })
        return
      }
      updateListApi({
        formIdent: this.$attrs['form-ident'],
        formHeadSets: this.tableData.map((item, key) => {
          return { ...item, sort: key + 1 }
        })
      }).then(res => {
        const { code } = res
        if (!code) {
          this.$notify.success({
            title: '成功',
            message: `${this.title}编辑成功`
          })
          this.$listeners.refresh()
          this.$emit('submit')
          this.beforeClose()
        }
        this.loading = false
      }).catch(err => {
        console.log(err)
        this.loading = false
      })
    },
    // handleSwitchChange(data, prop, text) {
    //   this.$confirm(`此操作将${!data[prop] ? '关闭' : '开启'} ${text}, 是否继续`, '提示', {
    //     confirmButtonText: '确 定',
    //     cancelButtonText: '取 消',
    //     type: 'warning',
    //     lockScroll: false
    //   }).then(() => {
    //     this.handleSubmit().then(() => {
    //       this.$notify.success({
    //         title: '成功',
    //         message: `${text}状态更新成功`
    //       })
    //     }).catch(() => {
    //       data[prop] = !data[prop]
    //     })
    //   }).catch(() => {
    //     data[prop] = !data[prop]
    //   })
    // },
    handleDefault() {
      // this.tableData = [...this.defaultData]
      this.opened()
    },
    initSortTable() {
      if (!this.$refs.sortTable) return
      const el = this.$refs.sortTable.$el.querySelectorAll('.el-table__body-wrapper > table > tbody')[0]
      this.sortable = Sortable.create(el, {
        filter: '.filter',
        ghostClass: 'sortable-ghost',
        setData: function(dataTransfer) {
          dataTransfer.setData('Text', '我是一个正在移动的元素')
        },
        onEnd: evt => {
          const targetRow = this.tableData.splice(evt.oldIndex, 1)[0]
          this.tableData.splice(evt.newIndex, 0, targetRow)
        }
      })
    },
    // 禁止顺序改变
    rowClassName({ row, rowIndex }) {
      if (row.disabled) return 'filter'
      return ''
    },
    opened() {
      this.loading = true
      this.init().then(res => {
        this.tableData = _.cloneDeep(res)
        this.defaultData = _.cloneDeep(res)
        this.$nextTick(() => {
          this.initSortTable()
        })
        this.loading = false
      }).catch(err => {
        console.log(err)
        this.loading = false
      })
    },
    beforeClose(done) {
      this.tableData = []
      this.$emit('input', false)
      typeof done === 'function' && done()
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 440px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-bottom: 0;
}
.table-operation-icon {
  cursor: pointer;
}
</style>
