<template>
  <div>
    <template v-if="scope[column.prop]">
      <el-link size="mini" :style="{ 'white-space': 'nowrap', 'overflow': 'hidden', 'text-overflow': 'ellipsis'}" type="primary" @click="downloadFile(column,scope)">{{ scope[column.prop] }} </el-link>
    </template>
    <span v-else />
  </div>
</template>

<script>
import columnMixin from './column.mixin'

export default {
  name: 'UploadCom',
  mixins: [columnMixin],
  data() {
    return {
      BASE_API: `${process.env.VUE_APP_BASE_API}:8989`
    }
  },
  computed: {
    fileUrl() {
      const url = this.column.downloadFileUrl(this.scope)
      return url
    },
    isImg() {
      const fileName = this.scope[this.column.prop]
      const fileSuffix = fileName.substring(fileName.lastIndexOf('.') + 1)
      return ['png', 'jpg', 'jpeg', 'bmp', 'gif'].includes(fileSuffix)
    },
    isExcel() {
      const fileName = this.scope[this.column.prop]
      const fileSuffix = fileName.substring(fileName.lastIndexOf('.') + 1)
      return ['xlsx', 'xls', 'csv'].includes(fileSuffix)
    },
    isDoc() {
      const fileName = this.scope[this.column.prop]
      const fileSuffix = fileName.substring(fileName.lastIndexOf('.') + 1)
      return ['dox', 'docx'].includes(fileSuffix)
    }
  },
  methods: {
    // 下载文件  word和excel直接下载，图片和pdf直接打开   /sys/file/preview/fileId
    downloadFile(column, item) {
      if (this.isImg) {
        this.$viewerApi({
          options: {
            toolbar: false,
            navbar: false,
            title: false
          },
          images: [this.fileUrl]
        })
      } else {
        window.open(this.fileUrl)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.table-icon-com {
  font-size: 16px;
}
.download-url {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
