<template>
  <el-table-column
    :key="column.prop"
    :fixed="column.fixed"
    :prop="column.prop"
    :label="column.label"
    :width="column.width"
    :formatter="column.formatter"
    :sortable="column.sortable"
    :column-key="column.prop"
    :min-width="column.minWidth"
    :align="column.align"
    :show-overflow-tooltip="typeof column.showOverflowTooltip === 'boolean' ? column.showOverflowTooltip : true"
    :header-align="column.headAlign"
    :render-header="(h,obj) => renderHeader(h,obj,column)"
  >
    <template slot-scope="scope">
      <component :is="getComponentName(column)" :column="column" :scope="scope.row" :index="scope.$index" />
    </template>
    <template v-if="column.children && column.children.length">
      <table-column
        v-for="c in column.children"
        :key="c.prop"
        :column="c"
      />
    </template>
  </el-table-column>
</template>

<script>
import TableColumnComponents from './components' // 批量引入的Table行内组件
import TableColumn from './TableColumn.vue'

export default {
  name: 'TableColumn',
  components: {
    TableColumn,
    ...TableColumnComponents
  },
  props: {
    column: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {
      search: ''
    }
  },
  methods: {
    // 拼接列组件的名称
    getComponentName(column) {
      const is = column.module
      return `${is.charAt(0).toUpperCase()}${is.slice(1)}Com`
    },
    /*
    *  表头部重新渲染
    */
    renderHeader(h, { column, $index }, item) {
      const span = document.createElement('span')
      span.innerText = column.label
      document.body.appendChild(span)
      // 表头宽度设置为 表头宽度+10 或 minWidth 的最大值
      column.minWidth = Math.max(span.getBoundingClientRect().width + 30, column.minWidth)
      document.body.removeChild(span)
      if (item.headerSlot) {
        return item.renderHeader(h, { column, $index })
      } else {
        return h('span', column.label)
      }
    }
  }
}
</script>

