<template>
  <div>
    <el-progress
      :text-inside="column.textInside || true"
      :stroke-width="column.strokeWidth || 18"
      :percentage="typeof column.percentage === 'function' ? column.percentage(scope) : column.percentage"
      :color="typeof column.color === 'function' ? column.color(scope) : column.color"
      :format="format"
    />
  </div>
</template>

<script>
import columnMixin from './column.mixin'

export default {
  name: 'ProgressCom',
  mixins: [columnMixin],
  props: {

  },
  data() {
    return {
      format(percentage) {
        const formatMap = {
          25: 'BAD',
          50: 'LOW',
          100: 'GOOD'
        }
        return formatMap[percentage]
      }
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
