<template>
  <el-radio
    v-model="scope[column.prop]"
    :label="true"
    :disabled="column.setDisabled"
    @click.native.prevent="radioChange(scope, column)"
  >
    {{ '' }}
  </el-radio>
</template>

<script>
import columnMixin from './column.mixin'

export default {
  name: 'RadioCom',
  mixins: [columnMixin],
  props: {

  },
  data() {
    return {

    }
  },
  methods: {
    radioChange(scope, column) {
      if (column.setDisabled) return // 禁止状态不可点击
      this.column.statusClick(scope)
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
