export default {
  props: {
    // 页面columnList的某一项
    column: {
      type: Object,
      default: () => {
        return {
          prop: 'name',
          label: '名称',
          align: 'center',
          width: 120,
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null
        }
      }
    },
    scope: { // 表格行内数据
      type: Object,
      default: () => {
        return {}
      }
    },
    index: { // 点击的当前 scope.$index
      type: [Number, String],
      default: ''
    }
  }
}
