/*
*		使用方法:
*   引入：
*   import paginationPage from '@/mixins/paginationPage'
*   配置:
*   mixins: [paginationPage]
*/

import TableCom from '@/components/Table'
import Pagination from '@/components/Pagination'

export default {
  components: {
    TableCom,
    Pagination
  },
  data() {
    return {
      // 分页信息
      pageInfo: {
        current: 1, // 当前页码
        size: 50 // 每页条数
      },
      pages: 1, // 总共多少页
      total: 0// 总共条数
    }
  },
  methods: {
    // 搜索
    searchInit() {
      this.pageInfo = {
        current: 1, // 当前页码
        size: 50 // 每页条数
      }
      this.init()
    },
    initPage(pageInfo) {
      const { total, pageNum, pageSize } = pageInfo
      this.pageInfo.current = pageNum
      this.pageInfo.size = pageSize
      this.total = total
    },
    indexMethod(index) {
      const { current, size } = this.pageInfo
      return (index + 1) + (current - 1) * size
    },
    resetPage() {
      this.pageInfo = {
        current: 1, // 当前页码
        size: 50 // 每页条数
      }
      this.total = 0
    }
  }
}
