<template>
  <el-dialog
    :title="title"
    top="15vh"
    :visible="value"
    :show-close="true"
    width="750px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <el-table
        ref="sortTable"
        v-loading="loading"
        :data="tableData"
        style="width: 100%"
        border
        :header-cell-style="{background:'#eef1f6',color:'#606266',lineHeight: '10px'}"
      >
        <el-table-column
          label="序号"
          type="index"
          align="center"
          width="50"
        />
        <el-table-column
          label="模板名称"
          prop="templateName"
          align="center"
        />
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button type="primary" @click="handleModify(scope.row)">修 改</el-button>
            <el-button type="primary" @click="handleDelete(scope.row)">删 除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-scrollbar>
    <div slot="footer">
      <el-button @click="beforeClose">关 闭</el-button>
    </div>
    <sort-tem-add v-model="sortTemVisible" :edit-type="editType" title="表格排序模板修改" :dialog-form="dialogForm" @confirm="sortTemSaveModify" />
  </el-dialog>
</template>

<script>
import { getSortTableTemplateApi, modifySortTableTemplateApi, deleteSortTableTemplateApi } from '@/apis/modules/common'
import SortTemAdd from './SortTemAdd'

export default {
  components: {
    SortTemAdd
  },
  props: {
    value: {
      type: Boolean,
      default: false
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    title: {
      type: [String, Number],
      default: '表格排序模板'
    }
  },
  data() {
    return {
      tableData: [],
      loading: false,
      sortTemVisible: false,
      editType: 'add',
      dialogForm: {}
    }
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getSortTableTemplateApi({
          formIdent: this.$attrs['form-ident']
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 弹窗关闭前操作
    beforeClose(done) {
      typeof done === 'function' && done()
      this.$emit('input', false)
    },
    // 弹窗打开初始化
    initSelect() {
      this.init()
    },
    // 修改
    handleModify(item) {
      console.log('item', item)
      this.sortTemVisible = true
      this.editType = 'modify'
      this.dialogForm = item
    },
    // 保存修改
    sortTemSaveModify(params) {
      modifySortTableTemplateApi({
        ...params,
        formIdent: this.$attrs['form-ident']
      }).then(res => {
        const { code } = res
        if (!code) {
          this.$notify.success({
            title: '成功',
            message: `模板修改成功`
          })
          this.init()
          this.$emit('confirm')
        }
      }).catch(err => {
        console.log(err)
      })
    },
    // 删除
    handleDelete(item) {
      this.$confirm(`此操作将永久删除该模板, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        if (!item.id) return
        deleteSortTableTemplateApi({
          columnsTemplateId: item.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: `模板删除成功`
            })
            this.init()
            this.$emit('confirm')
          }
        })
      }).catch(() => {
        this.$message({
          message: '已取消删除',
          type: 'info'
        })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 440px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-bottom: 0;
}
</style>

